.header{
    width: 100%;
    
       .scontainer{

            .header__about{
                width: 100%;
                    .text{
                        width: 100%;
                        text-align: center;
                        .about__h5{
                            font-size: 17px;
                            margin-top: 50px;
                            color: rgb(153, 120, 13);
                        }
                        .about__h1{
                            margin-top: 30px;
                            color: black;
                            font-size: 35px;
                        }
                        .about__p{
                            margin-top: 30px;
                        }
                        .i{
                            color: rgb(153, 120, 13);
                            margin-top: 30px;
                        }
                    }
            }
       } 
}
@media only screen and (max-width: 768px) {
    .header{
        width: 100%;
        
           .scontainer{
    
                .header__about{
                    width: 100%;
                        .text{
                            width: 100%;
                            text-align: center;
                            .about__h5{
                                font-size: 15px;
                                margin-top: 50px;
                                color: rgb(153, 120, 13);
                            }
                            .about__h1{
                                margin-top: 30px;
                                color: black;
                                font-size: 25px;
                            }
                            .about__p{
                                font-size: 12px;
                                margin-top: 30px;
                            }
                            .i{
                                color: rgb(153, 120, 13);
                                margin-top: 30px;
                            }
                        }
                }
           } 
    }
}
@media only screen and (max-width: 500px) {
    .header{
        width: 100%;
        
           .scontainer{
    
                .header__about{
                    width: 100%;
                        .text{
                            width: 100%;
                            text-align: center;
                            .about__h5{
                                font-size: 12px;
                                margin-top: 50px;
                                color: rgb(153, 120, 13);
                            }
                            .about__h1{
                                margin-top: 30px;
                                color: black;
                                font-size: 18px;
                            }
                            .about__p{
                                font-size: 9px;
                                margin-top: 30px;
                            }
                            .i{
                                color: rgb(153, 120, 13);
                                margin-top: 30px;
                            }
                        }
                }
           } 
    }
}